___CSS_LOADER_EXPORT___.push([module.id, `h1 {
    font-size: var(--font-head);
    font-weight: 700;
    margin-top: 15vh;
}

.subhead {
    font-size: var(--font-lg);
    margin-top: 2rem;
}

.wrapper {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

form {
    margin-top: 4rem;
    width: 80vw;
    max-width: 50rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "player-1 player-2"
    "color color"
    "mode mode"
    "submit submit";
    gap: 2rem;
    /* background-color: beige; */
}

.player-div-1 {
    grid-area: player-1;
    display: grid;
}

.player-div-2 {
    grid-area: player-2;
}

[class*="player-div"] {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.color {
    grid-area: color;
    display: flex;
    flex-direction: column;
}

.mode {
    display: flex;
    flex-direction: column;
    grid-area: mode;
}

.color > p,
.mode > p {
    text-align: center;
    margin-bottom: 1rem;
    font-weight: 600;
}

.color > div,
.mode > div {
    display: flex;
    justify-content: space-around;
    gap: 1rem;
}

.color > div > div,
.mode > div > div {
    display: flex;
    gap: .5rem;
    align-items: center;
}

.color > div > div {
    gap: .2;
}

button {
    grid-area: submit;
    min-width: 7rem;
    padding: .3rem;
    margin: auto;
    background: gray;
    border: none;
    border-radius: .5rem;
}

[class*="player"] input {
    border: none;
    background-color: gray;
    width: 100%;
    padding: .2rem;
    padding-inline: .5rem;
    cursor: auto;
}`, "",{"version":3,"sources":["webpack://./src/css/home.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;IAC3B,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B;;;mBAGe;IACf,SAAS;IACT,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,eAAe;AACnB;;AAEA;;IAEI,kBAAkB;IAClB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;;IAEI,aAAa;IACb,6BAA6B;IAC7B,SAAS;AACb;;AAEA;;IAEI,aAAa;IACb,UAAU;IACV,mBAAmB;AACvB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,cAAc;IACd,YAAY;IACZ,gBAAgB;IAChB,YAAY;IACZ,oBAAoB;AACxB;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,WAAW;IACX,cAAc;IACd,qBAAqB;IACrB,YAAY;AAChB","sourcesContent":["h1 {\n    font-size: var(--font-head);\n    font-weight: 700;\n    margin-top: 15vh;\n}\n\n.subhead {\n    font-size: var(--font-lg);\n    margin-top: 2rem;\n}\n\n.wrapper {\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\nform {\n    margin-top: 4rem;\n    width: 80vw;\n    max-width: 50rem;\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-template-areas: \"player-1 player-2\"\n    \"color color\"\n    \"mode mode\"\n    \"submit submit\";\n    gap: 2rem;\n    /* background-color: beige; */\n}\n\n.player-div-1 {\n    grid-area: player-1;\n    display: grid;\n}\n\n.player-div-2 {\n    grid-area: player-2;\n}\n\n[class*=\"player-div\"] {\n    display: flex;\n    flex-direction: column;\n    gap: .5rem;\n}\n\n.color {\n    grid-area: color;\n    display: flex;\n    flex-direction: column;\n}\n\n.mode {\n    display: flex;\n    flex-direction: column;\n    grid-area: mode;\n}\n\n.color > p,\n.mode > p {\n    text-align: center;\n    margin-bottom: 1rem;\n    font-weight: 600;\n}\n\n.color > div,\n.mode > div {\n    display: flex;\n    ju